import React from 'react';
import Grid from '@material-ui/core/Grid';
import FormLabel from '@material-ui/core/FormLabel';
import { makeStyles } from '@material-ui/core/styles';

import { CurrencyField, DateField } from '../../../../../components/Fields';
import STRINGS from '../../../../../strings';
import { Props } from '../index';
import { MRG_BOTT } from '../../../../Quotation/PersonalLoan/types';
import TextFieldF from '../../../../../components/Fields/TextFieldF';

const { WORK } = STRINGS.APPLICATION.PERSONAL_LOAN;

const useStyles = makeStyles({
  bottomSpacing: {
    marginBottom: MRG_BOTT.bottomSpacing
  },
  label: {
    marginBottom: MRG_BOTT.label
  }
});

function Work(props: Props) {
  const classes = useStyles();
  const { application: { borrower }, setApplication } = props;

  function onApplicationChange(key: string, value: string) {
    setApplication({
      borrower: {
        [key]: value
      },
    });
  }

  function onWageChange(wage: string) {
    setApplication({ borrower: { wage: Number(wage) || undefined } });
  }

  function onStartDateSelected(startDate?: number | null): void {
    if (startDate) {
      setApplication({
        borrower: { startDate },
      });
    }
  }

  const phoneRegex = /^\d{3}(-|\s)\d{4}$/
  const verifyNoIP  = /^\d{7}$/

  const errExtension = (ext: string) => {
    let Err = false
    if(ext) {
      if(!ext.match(/\d/g)) {
        Err = true
      } else if(ext?.length > 4) {
        Err = true
      }
    }
    return Err
  }


  return (
    <Grid id="top" container spacing={2} justify='center'>
      <Grid container item xs={12} sm={11}>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={WORK.COMPANY}
            onChange={(value: string) => onApplicationChange('company', value)}
            value={borrower.company}
            error={!borrower.company || borrower.company.length > 80}
            message={!borrower.company ? "Este es un campo Obligatorio ...": (borrower.company.length > 80 ? "Máximo 80 caracteres." : "")}         
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={WORK.POSITION}
            onChange={(value: string) => onApplicationChange('position', value)}
            value={borrower.position}
            error={!borrower.position || borrower.position.length > 60}
            message={!borrower.position ? "Este es un campo Obligatorio ...": (borrower.position.length > 60 ? "Máximo 60 caracteres." : "")}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={"No. IP"}
            onChange={(value: string) => onApplicationChange('noIp', value)}
            value={borrower.noIp}
            error={!borrower.noIp || borrower.noIp.length > 7 || !verifyNoIP.test(borrower.noIp)}
            message={!borrower.noIp ? "Este es un campo Obligatorio ...": (borrower.noIp.length > 7 ? "Máximo 7 números." : (!verifyNoIP.test(borrower.noIp) ? "Ej. 1234567" : ""))}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={"Departamento"}
            onChange={(value: string) => onApplicationChange('department', value)}
            value={borrower.department}
            error={!borrower.department || borrower.department.length > 60}
            message={!borrower.department ? "Este es un campo Obligatorio ...": (borrower.department.length > 60 ? "Máximo 60 caracteres." : "")} 
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={"Especialidad"}
            onChange={(value: string) => onApplicationChange('specialty', value)}
            value={borrower.specialty}
            error={!borrower.specialty || borrower.specialty.length > 60}
            message={!borrower.specialty ? "Este es un campo Obligatorio ...": (borrower.specialty.length > 60 ? "Máximo 60 caracteres." : "")} 
          />
        </Grid>

        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <DateField 
            label={"Fecha de Ingreso"}
            onChange={onStartDateSelected}
            value={borrower.startDate}
          />
        </Grid>

        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
            label={WORK.ADDRESS}
            onChange={(value: string) => onApplicationChange('companyAddress', value)}
            value={borrower.companyAddress}
            error={!borrower.companyAddress || borrower.companyAddress.length > 60}
            message={!borrower.companyAddress ? "Este es un campo Obligatorio ...": (borrower.companyAddress.length > 60 ? "Máximo 60 caracteres." : "")}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <Grid container item xs={12} sm={6} spacing={2}>
            <Grid item xs={8}>
              <TextFieldF
                label={WORK.PHONE_NUMBER}
                onChange={(value: string) => onApplicationChange('companyPhoneNumber', value)}
                value={borrower.companyPhoneNumber}
                error={!borrower.companyPhoneNumber || borrower.companyPhoneNumber.length > 8 || !phoneRegex.test(borrower.companyPhoneNumber)}
                message={!borrower.companyPhoneNumber ? "Este es un campo Obligatorio ...": (borrower.companyPhoneNumber.length > 8 ? "Máximo 7 números." : (!phoneRegex.test(borrower.companyPhoneNumber) ? "Ej. 320-2323" : ""))}
              />
            </Grid>
            <Grid item xs={4}>
              <TextFieldF
                label={"Extensión"}
                onChange={(value: string) => onApplicationChange('companyPhoneExtension', value)}
                value={borrower.companyPhoneExtension}
                error={errExtension(borrower.companyPhoneExtension || "")}
                message={(borrower.companyPhoneExtension && borrower.companyPhoneExtension.length > 4 ? "Máximo 4 números." : (borrower.companyPhoneExtension && !borrower.companyPhoneExtension.match(/\d/g) ? "Sólo números" : ""))}
              />
            </Grid>
          </Grid>
        </Grid>

        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <Grid container item xs={12} sm={6} spacing={2}>
            <Grid item xs={8}>
              <TextFieldF
                label={"Teléfono 2"}
                onChange={(value: string) => onApplicationChange('companyPhoneNumber2', value)}
                value={borrower.companyPhoneNumber2}
                error={(borrower.companyPhoneNumber2 || "").length > 0 && !phoneRegex.test(borrower.companyPhoneNumber2 || "")}
                message={(borrower.companyPhoneNumber2 || "").length > 0 &&  !phoneRegex.test(borrower.companyPhoneNumber2 || "") ? "Ej. 320-2323" : ""}
               />
            </Grid>
            <Grid item xs={4}>
              <TextFieldF
                label={"Extensión"}
                onChange={(value: string) => onApplicationChange('companyPhoneExtension2', value)}
                value={borrower.companyPhoneExtension2}
                error={errExtension(borrower.companyPhoneExtension2 || "")}
                message={(borrower.companyPhoneExtension2 && borrower.companyPhoneExtension2.length > 4 ? "Máximo 4 números." : (borrower.companyPhoneExtension2 && !borrower.companyPhoneExtension2.match(/\d/g) ? "Sólo números" : ""))}
              />
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <TextFieldF
              label={WORK.PREVIOUS_COMPANY}
              onChange={(value: string) => onApplicationChange('previousCompany', value)}
              value={borrower.previousCompany}
              error={(borrower?.previousCompany || "").length > 60}
              message={borrower.previousCompany && borrower.previousCompany.length > 60 ? "Máximo 60 caracteres." : ""} 
            />
        </Grid>
        <Grid item xs={12} classes={{item: classes.label}}>
          <FormLabel>{WORK.WAGE}</FormLabel>
        </Grid>
        <Grid item xs={12} sm={6} classes={{item: classes.bottomSpacing}}>
          <CurrencyField
            onChange={onWageChange}
            value={borrower.wage}
            endLabel
          />
        </Grid>
      </Grid>
    </Grid>
  )
}

export default Work;