import React from 'react';
import useFetch from 'use-http';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Props } from '../index';

import Form from './Form';

export interface IMarca {
  id: number;
  name: string;
}
export interface IModelo {
  id: number;
  id_marca: number;
  name: string;
}
export interface IProduct {
  id: number;
  name: string;
  tasa: number;
  tasa2: number;
  plazo_max: number;
  edad_max_mujer: number;
  edad_max_hombre: number;
  gastos_notaria: number;
  factor: number;
}
export interface ITarifa {
  id: number;
  id_marca: string;
  id_modelo: number;
  anio_auto: number;
  tasa: number;
  tasa2: number;
  plazo_max: number;
}

export interface LaboralSector {
  id: number;
  sector: string;
  id_sector: number,
  name: string;
}

export interface TermLoan {
  id: number;
  name: number;
}

export interface LaboralEntity {
  ruta: string,
  id_sector: number,
  id_profesion: number,
  discount_capacity: number;
  discount_capacity_mortgage: number;
  debt_capacity: number;
  debt_capacity_mortgage: number;
  plazo_max: number;
  tasa: number;
  comision: number;
  salario_min: number;
  tasa_servicio: number;
  seg_vida: number;
  feci: number;
  itbms: number;
  notaria: number;
  mount_min: number;
  mount_max: number;
  factor: number;
  factor_SV: number;
  letraRetenida: boolean;
  gastoLegal: number;
  timbres: number;
  servicioDescto: number;
  min_antiguedad: number;
}

export interface Loan {
  bank: string;
  loan: number;
  term: number;
  paysYear: number;
  monthlyFee: number;
  cashOnHand: number;
}


function Terms(props: Props) {
  const { data: sectorResponse, loading: sectorLoading  } = useFetch<LaboralSector[]>(
    '/laboral_sector', 
    { method: 'GET' }, 
    []
  );

  const { data: termsLoan, loading: loadingTermL } = useFetch<TermLoan[]>(
    "/terms_loan",
    { method: "GET" },
    []
  );

  const { data: laboralEntitys, loading } = useFetch<LaboralEntity[]>(
    "/laboral_sector_entity_f",
    { method: "GET" },
    []
  );

  const { data: marcas, loading: loadingMarcas } = useFetch<IMarca[]>(
    "/marcas_autos",
    { method: "GET" },
    []
  );
  const { data: modelos, loading: loadingModelos } = useFetch<IModelo[]>(
    "/modelos_autos",
    { method: "GET" },
    []
  );
  const { data: tarifas, loading: loadingtarifas } = useFetch<ITarifa[]>(
    "/tarifas_auto",
    { method: "GET" },
    []
  );
  const { data: products, loading: loadingProducts } = useFetch<IProduct[]>(
    "/cooprac_product",
    { method: "GET" },
    []
  );
  if (loading || !laboralEntitys || loadingTermL || !termsLoan
    || sectorLoading || !sectorResponse
    || loadingMarcas || !marcas || loadingModelos || !modelos
    || loadingtarifas || !tarifas || loadingProducts || !products
  ) {
    return (
      <div style={{
        display:'flex',
        height:'100%',
        justifyContent: 'center',
        alignItems: 'center'
      }}>
        <CircularProgress />
      </div>
    );
  }

  return <Form 
    {...props} 
    laboralEntitys={laboralEntitys}
    termsLoan={termsLoan}
    sectors={sectorResponse}
    tmarcas={marcas}
    tmodelos={modelos}
    ttarifas={tarifas}
    tproducts={products}
  />
};

export default Terms;