import React from 'react';
import Grid from '@material-ui/core/Grid';
import DriveEtaOutlinedIcon from '@material-ui/icons/DriveEtaOutlined';
import BuildOutlinedIcon from '@material-ui/icons/BuildOutlined';
import FlightTakeoffOutlinedIcon from '@material-ui/icons/FlightTakeoffOutlined';
import FavoriteBorderOutlinedIcon from '@material-ui/icons/FavoriteBorderOutlined';
import SchoolOutlinedIcon from '@material-ui/icons/SchoolOutlined';
import { SvgIconProps } from '@material-ui/core/SvgIcon';
import STRINGS from '../../../../../strings';
import { CardRadioButtons } from '../../../../../components/RadioButtons';
import { MoneyIcon } from '../../../../../components/Svg';

import { LOAN_PURPOSES } from '../../types';
import { Props as QuotationProps } from '../index';
import { Purpose } from './index';
import TextFieldF from '../../../../../components/Fields/TextFieldF';

function getPurposeIcon(): Record<string, React.ElementType<SvgIconProps>> {
  return {
    [LOAN_PURPOSES.CAR_PURCHASE]: DriveEtaOutlinedIcon,
    [LOAN_PURPOSES.WEDDING]: FavoriteBorderOutlinedIcon,
    [LOAN_PURPOSES.HOME_IMPROVEMENTS]: BuildOutlinedIcon,
    [LOAN_PURPOSES.SCHOOL]: SchoolOutlinedIcon,
    [LOAN_PURPOSES.TRAVEL]: FlightTakeoffOutlinedIcon,
    [LOAN_PURPOSES.TEENAGE_PARTY]: MoneyIcon
  };
};

interface Props extends QuotationProps {
  loanPurposes: Purpose[]
};

function Reason(props: Props) {
  const { loanPurposes, quotation, setQuotation } = props;

  function onOptionPressed(reason: string) {
    setQuotation({ loan: { reason }});
  }

  function onReasonChange(otherReason: string) {
    setQuotation({ loan: { otherReason: otherReason }});
  }

  const options = loanPurposes.map((purpose, idx) => ({
    value: String(idx),
    label: idx===5 ? "Otro" : purpose.name,
    Icon: getPurposeIcon()[String(idx)]
  }));

  return (
    <Grid container spacing={2} justify='center'>
      <Grid item xs={11}>
        <CardRadioButtons 
          label={STRINGS.QUOTATION.PERSONAL_LOAN.LOAN.CHOOSE_REASON}
          onClick={onOptionPressed}
          options={options}
          value={quotation.loan.reason}
        />
      </Grid>
      {quotation.loan.reason === '5' ? (
        <Grid item xs={11}>
          <TextFieldF
            label={"Otro propósito, Indiquelo:"}
            onChange={onReasonChange}
            value={quotation.loan.otherReason}
            error={!quotation.loan.otherReason || quotation.loan.otherReason.length > 60}
            message={!quotation.loan.otherReason ? "Este es un campo Obligatorio ...": (quotation.loan.otherReason.length > 60 ? "Máximo 60 caracteres." : "")}
          />
        </Grid>
      ) : null}
    </Grid>
  );
};

export default Reason;