import React from 'react';
import Container from '@material-ui/core/Container';
import Typography from '@material-ui/core/Typography';
import { makeStyles, Theme } from '@material-ui/core/styles';

import Stepper from '../../../components/Stepper';
import PRODUCTS from '../../../lib/products';
import useStep from './hooks/useStep';
import useQuotation from './hooks/useQuotation';

const useStyles = makeStyles((theme: Theme) => ({
  container: {
    display: 'flex', 
    flexDirection: 'column', 
    height: '100%', 
    paddingTop: 52, 
    paddingBottom: 72,
    [theme.breakpoints.down('sm')]: {
      paddingTop: 24, 
      paddingBottom: 24
    }
  },
  title: {
    display: 'flex',
    alignItems: 'center',
    marginBottom: 52
  }
}));

// paddingTop: 72, 
// marginBottom: 72

function PersonalLoan() {
  const [quotation, setQuotation] = useQuotation();
  const { next, prev, disabled, Form } = useStep(quotation, setQuotation);
  const classes = useStyles();

  // LLRR: 2022-01-20
  const productName = () => {
    const product = window.localStorage.getItem("product") || "1"
    let name = PRODUCTS.PERSONAL_LOAN.name
    if(product === "3") {
      name = PRODUCTS.CAR_LOAN.name
    } else if(product === "4") {
      name = PRODUCTS.MORTGAGE.name
    }
    return name
  } 
  // LLRR: 2022-01-20
  const productIcon = () => {
    const product = window.localStorage.getItem("product") || "1"
    let icon = <PRODUCTS.PERSONAL_LOAN.icon color='primary' fontSize='large' style={{ marginRight: 16 }}/>
    if(product === "3") {
      icon = <PRODUCTS.CAR_LOAN.icon color='primary' fontSize='large' style={{ marginRight: 16 }}/>
    } else if(product === "4") {
      icon = <PRODUCTS.MORTGAGE.icon color='primary' fontSize='large' style={{ marginRight: 16 }}/>
    }
    return icon
  } 

  return (
    <Container classes={{root: classes.container}}>
      <Typography classes={{root: classes.title}} variant='h5'>
        {/* <PRODUCTS.PERSONAL_LOAN.icon color='primary' fontSize='large' style={{ marginRight: 16 }}/> */}
        {/* {PRODUCTS.PERSONAL_LOAN.name} */}
        {productIcon()}
        {productName()}
      </Typography>
      <div style={{flex:1, marginBottom: 56}}>
        {Form}
      </div>
      <Stepper onNext={next} onPrev={prev} disabled={disabled} />
    </Container>
  );
};

export default PersonalLoan;