import React from 'react';
import useFetch from 'use-http';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Props } from '../index';
import Form from './Form';

export interface iApc {
  status: boolean;
  message: string;

  Agente_Economico: string; 
  Dias_Atraso: number;
  Estado: string; 
  Fec_Actualiazacion: string; 
  Fec_Ini_Relacion: string; 
  Fec_Prescripcion: string; 
  Fec_Ultimo_pago: string; 
  Fec_Vencimiento: string; 
  Forma_Pago: string; 
  Historial: string; 
  Letra: number;
  Monto_Original: number;
  Monto_Utimo_Pago: number;
  Num_Pagos: number;
  Observacion: string; 
  Referencia: string; 
  Relacion: string; 
  Saldo_Actual: number;

  score: string;
  pi: string;
  exclusion: string; 
  montoAplicado: number;
  montoARecibir: number;
}

function Aproach(props: Props) {

  const { application: { borrower: { id } } } = props;

  //"/leerAPC",
  const { data: refApc, loading } = useFetch<iApc[]>(
    "/APC",
    { 
      method: "POST",
      body: JSON.stringify({ "id": id, "tipoCliente": "1", "productoApc": "1" })
    },
    []
  );
  
  if (loading || !refApc) {
    return (
      <div style={{
        display:'flex',
        height:'100%',
        justifyContent: 'center',
        alignItems: 'center'
      }}>
        <CircularProgress />
      </div>
    );
  }

  return <Form {...props} refApc={refApc} />;
};

export default Aproach;