
import React, { useEffect, useState } from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { makeStyles, Theme } from '@material-ui/core/styles';
import moment from 'moment'

import CheckCircleOutlineOutlinedIcon from '@material-ui/icons/CheckCircleOutlineOutlined';
import { StarsBackgroundPath } from '../../../../../components/Images';
import STRINGS from '../../../../../strings';
import { LaboralEntity, TermLoan, LaboralSector, IProduct } from './index';
import { Props as QuotationProps } from '../index';


import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from  '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { Button, Checkbox, FormControlLabel, FormLabel } from '@material-ui/core';
import { NumberField } from '../../../../../components/Fields';

const useStyles = makeStyles((theme: Theme) => ({
  container: {
    display: 'flex', 
    flexDirection: 'column', 
    height: '100%', 
    paddingTop: 72, 
    paddingBottom: 72,
    [theme.breakpoints.down('sm')]: {
      paddingTop: 24, 
      paddingBottom: 24
    }
  },
  title: {
    display: 'flex',
    alignItems: 'center',
    marginBottom: 72
  },
  subtitle: {
    '& span': {
      fontWeight: theme.typography.fontWeightBold,
      color: theme.palette.primary.main
    }
  },
  content: {
    textAlign:'center',
    marginBottom: 80
  },
  icon: {
    color: theme.palette.success.main,
    height: 120,
    width: 120,
    marginBottom: 36
  },
  label: {
    marginBottom: 10
  },
  starsBackground: {
    backgroundImage: `url(${StarsBackgroundPath})`,
    backgroundSize: 'contain',
    backgroundPosition: 'center',
    backgroundRepeat: 'no-repeat'
  }
}));
    
interface Props extends QuotationProps {
  laboralEntitys: LaboralEntity[];
  termsLoan: TermLoan[];
  sectors: LaboralSector[];
  tproducts: IProduct[];
};

interface IDetPagos {
  cuota: number;
  desde: string;
  hasta: string;
  dias: number;
  letra: number;
  tasa: number;
  saldoAnt: number;
  pagoCap: number;
  intCal: number;
  pagoInt: number;
  intPend: number;
  saldoAct: number;
}

function Form(props: Props) {
  const { quotation, setQuotation, laboralEntitys, tproducts } = props;
  const classes = useStyles();
  const [ checked, setChecked] = useState(false);
  const [plazoMax, setPlazoMax] = useState(0)
  const product = "1"

  let { monthlyResidenceFee = 0.00, wage = 0.00, alloance = 0.00, perDiem = 0.00 } = quotation.borrower

  //  Status Laboral
  //  0	Temporal	
  //  1	Permanente	
  //  2	Serv. Profesional	

  let { gender, birthDate } = quotation.borrower;
  let salario = wage + alloance + perDiem

  const today = new Date() ; //  Ver como traerla del servidor
  let birth = new Date();
  if (birthDate !== undefined) {
    birth = new Date(birthDate)
  }

  let dd = today.getDate()
  let mm = today.getMonth() + 1
  let yyyy = today.getFullYear() 

  let d1 = dd<10?'0'+dd.toString():dd.toString()
  let m1 = mm<10?'0'+mm.toString():mm.toString()

  dd = birth.getDate()
  mm = birth.getMonth() + 1
 
  d1 = dd<10?'0'+dd.toString():dd.toString()
  m1 = mm<10?'0'+mm.toString():mm.toString()

  const [detPagos, setDetPagos] = useState<IDetPagos[]>([])
  const [montoSol, setMontoSol] = useState(0)
  const [montoFinal, setMontoFinal] = useState(0)
  const [plazoSol, setPlazoSol] = useState(0)
  const [plazoFinal, setPlazoFinal] = useState(0)
  const [cuotaMax, setCuotaMax] = useState(0)
  // const [cuotaSol, setCuotaSol] = useState(0)
  const [cuotaFinal, setCuotaFinal] = useState(0)
  const [tasaSol, setTasaSol] = useState(0)
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [tasaSol2, setTasaSol2] = useState(0)

  //-- LLRR cambiado para los calculos por cada entidad
  // Falta incluir filtro para la antiguedad minima en el empleo (meses) 22-nov-2021
  const laboralEntities = laboralEntitys.filter(
    le => le.ruta === "810" && le.id_profesion === 5 
    && le.salario_min <= salario)

  const tproduct = tproducts.filter( p => p.id === Number(product))

  function handleCapacity(tasa: number) {

    tasa = tasa/100

    yyyy = birth.getFullYear() + (gender==='male' ? 62 : 57)
    // Fecha de Jubilacion
    let fj = new Date(yyyy.toString()+'-'+m1.toString()+'-'+d1.toString())

    //***************************************************************/
    //***************************************************************/
    //** CALCULO STANDAR  **/
    //***************************************************************/
    //***************************************************************/

    let monto_sol = montoSol
    // let letra = cuotaSol
    let letra = cuotaMax
    let letra_bk = letra
    let ciclos = 1

    let wrkV2:IDetPagos[] = []

    wrkV2 = calc_montos(plazoSol, fj)
    setDetPagos(wrkV2)
 
    let saldo_act = 0
    let saldo_ant = -9999
    let int_pend = 0
    let logg = []

    const ajusteMonto = () => {
      for (let i=0; i<wrkV2.length; i++) {

        let cuota = wrkV2[i] 
        let f1 = new Date(cuota.hasta)
        let f2 = new Date(cuota.desde)
        let dias = (f1.getTime() - f2.getTime()) / (24*60*60*1000)
        if(saldo_ant === -9999) {
          saldo_ant = monto_sol
        }

        let pago = letra
        pago -= int_pend
        let int_cal = Math.round(saldo_ant*tasa*dias/365*100)/100
        pago -= int_cal
        let tint_pend = pago < 0 ? pago*(-1) : 0
        let pago_int = pago > 0 ? int_cal : int_cal + pago
        saldo_act = saldo_ant - (pago < 0 ? 0 : pago)

        logg.push({
          "cuota": i, 
          "saldo": saldo_act
        })

        wrkV2[i].dias = dias
        wrkV2[i].saldoAnt = saldo_ant
        wrkV2[i].saldoAct = saldo_act
        wrkV2[i].intCal = int_cal
        wrkV2[i].pagoInt = pago_int
        wrkV2[i].letra = letra
        wrkV2[i].tasa = Math.round(tasa*100/12*100)/100
        if(pago > 0) {
          wrkV2[i].pagoCap = pago
          wrkV2[i].intPend = 0
        } else {
          wrkV2[i].pagoCap = 0
          wrkV2[i].intPend = tint_pend
        }

        if(saldo_act <= 0) {
          break;
        }

        saldo_ant = saldo_act
        int_pend = tint_pend
        if(saldo_ant < cuota.letra) {
          // eslint-disable-next-line @typescript-eslint/no-unused-vars
          let dias = 30.465
          if(i < wrkV2.length - 1) {
            let cuota = wrkV2[i+1] 
            let f1 = new Date(cuota.hasta)
            let f2 = new Date(cuota.desde)
            dias = (f1.getTime() - f2.getTime()) / (24*60*60*1000)
          }

          // letra = saldo_ant + Math.round(saldo_ant*tasa*dias/365*100)/100
          letra = saldo_ant + Math.round(saldo_ant*tasa/12*100)/100
        }

        ciclos += 1
      }
      setDetPagos(wrkV2)
    }

    do {
      ciclos = 1
      saldo_ant = -9999
      int_pend = 0
      logg = []

      ajusteMonto()
      if(saldo_act <= 0 && ciclos >= plazoSol) {
        setMontoFinal(monto_sol)
        setMontoSol(monto_sol)
        setPlazoFinal(ciclos)
        setQuotation({
          loan: { 
            term: ciclos - 1, bank: "810", amount: monto_sol, 
            paysYear: undefined,  cashOnHand: monto_sol,
            abonoAuto: undefined,
            monthlyFee: letra_bk, monthlyFee2: Number(wrkV2[wrkV2.length-1].letra.toFixed(2))
          }
        })
        break;
      }
      if(saldo_act > 0 && ciclos >= plazoSol) {
        monto_sol -= 5
        letra = letra_bk
      }
      if(saldo_act <= 0 && ciclos < plazoSol) {
        letra_bk -= 0.25
        letra = letra_bk
        saldo_act = 1
        setCuotaFinal(Number(letra.toFixed(2)))
        // setCuotaSol(letra)
      }
    } while (saldo_act !== 0 && letra_bk > 0) 
  }

  const calc_montos = (plazoCalc: number, fj: Date) => {
    const fcot = new Date()
    let yyyy = fcot.getFullYear()
    let mm = fcot.getMonth() + 2
    let dd = fcot.getDate()
    let fecIni = yyyy.toString() + '-' + ((mm < 10) ? '0'+mm.toString() : mm.toString()) + '-' + ((dd < 10) ? '0'+dd.toString() : dd.toString())
   
    if(mm === 11) {
      mm = 1
      yyyy += 1
    }
    else {
      mm += 1
      if(mm > 12) {
        mm = 1
        yyyy += 1
      }
    }

    let dd2 = dd
    if(dd2 > 30 && (mm === 4 || mm === 6 || mm === 9 || mm === 11 )) dd = 30
    else if(dd2 > 27 && mm === 2) {dd = 28}
    else {dd = dd2}

    dd += 1
    let ftemp = yyyy.toString() + '-' + ((mm < 10) ? '0'+mm.toString() : mm.toString()) + '-' + ((dd < 10) ? '0'+dd.toString() : dd.toString())
    let ffpay = new Date(ftemp)

    let ff = ffpay
    
    yyyy = ffpay.getFullYear()
    mm = ffpay.getMonth() + 1
    dd = ffpay.getDate()

    dd2 = dd
    ftemp = ''
    let wDic = 0
    let wPlazo = plazoCalc

    let i = 0
    let detPagos: IDetPagos[] = []

    while (i < wPlazo) {
      if(mm === 12) {
        mm = 1
        yyyy += 1
        wDic = wDic + 1
      }

      if(dd2 > 30 && (mm === 4 || mm === 6 || mm === 9 || mm === 11 )) dd = 30
      else if(dd2 > 27 && mm === 2) {dd = 28}
      else {dd = dd2}

      ftemp = yyyy.toString() + '-' + ((mm < 10) ? '0'+mm.toString() : mm.toString()) + '-' + ((dd < 10) ? '0'+dd.toString() : dd.toString())

      const f = new Date(ftemp)
      ff = new Date(f.getTime()) // + 24*60*60*1000)

      if (ff > fj) {
        break
      }

      mm += 1
      i += 1

      let item = {
        "cuota": i, 
        "desde": fecIni,
        "hasta": ftemp,
        "letra": 0,
        "tasa": 0,
        "dias": 0,
        "saldoAnt": 0,
        "pagoCap": 0,
        "intCal": 0,
        "pagoInt": 0,
        "intPend": 0,
        "saldoAct": 0,
      }
      detPagos.push(item)
      fecIni = ftemp
    }

    return detPagos
  }

  const calculosPlazo = () => {
    handleCapacity(tasaSol)
  }

  const handleTermBlur = (plazo: string) => {
    console.log(plazo, plazoMax)
    debugger
    if(Number(plazo) > plazoMax) {
      setPlazoSol(plazoMax)
    }
  }

  const handleAmountChange = (amount: string) => {
    let monto = Number(amount);
    setMontoSol(monto);
    setQuotation({ loan: { amount: undefined }});
    let plazo = 96
    if(monto > 10000) plazo = 120
    if(monto > 15000) plazo = 180
    if(monto > 20000) plazo = 240
    setPlazoSol(plazo)
    setPlazoMax(plazo)
  }
  const handleTermChange = (plazo: string) => {
    setPlazoSol(Number(plazo));
    setQuotation({ loan: { amount: undefined }});
  }
  const handleCuotaChange = (cuota: string) => {
    // setCuotaSol(Number(cuota));
    setCuotaFinal(Number(cuota));
    setQuotation({ loan: { amount: undefined }});
  }
  const handleTasaChange = (cuota: string) => {
    setTasaSol(Number(cuota));
    setQuotation({ loan: { amount: undefined }});
  }

  useEffect(() => {
    const entity = laboralEntities[0]
    let { plazo_max, debt_capacity, debt_capacity_mortgage, 
      discount_capacity, discount_capacity_mortgage 
    } = entity

    let tasa = laboralEntities[0].tasa
    let tasa2 = 0
    const { 
      tasa: prod_tasa, tasa2: prod_tasa2, plazo_max: prod_plazo_max
    } = tproduct[0]
    if(prod_tasa > 0) tasa = prod_tasa
    if(prod_tasa2 > 0) tasa2 = prod_tasa2
    if(prod_plazo_max > 0) plazo_max = prod_plazo_max

    const bruto = wage + alloance + perDiem
    const salary = wage
    const gastosRep = alloance + perDiem

    const ss = bruto * 9.75/100
    const se = bruto * 1.25/100
    let isr = Math.round(gastosRep*10)/100
    if(salary*13 > 11000) {
      isr += Math.round((salary*13-11000)*15)/100/13
    } else if(salary*13 > 50000) {
      isr += Math.round((salary*13-50000)*25)/100/13
    }
  
    let salaryNet = (salary + gastosRep) - (ss + se + isr)
    salaryNet = Number(salaryNet.toFixed(2))
    
    let mortage = monthlyResidenceFee
    let pje_dscto = discount_capacity
    if(mortage > 0) pje_dscto = discount_capacity_mortgage
    let pje_debt = debt_capacity
    if(mortage > 0) pje_debt = debt_capacity_mortgage

    const cap_discto = salaryNet * pje_dscto / 100.00
    const cap_debt = salaryNet * pje_debt / 100.00

    const cap_debt_available = cap_debt - mortage
    let cap_discto_available = cap_debt_available < cap_discto ? cap_debt_available : cap_discto
    cap_discto_available = Number(cap_discto_available.toFixed(2))
    setQuotation({ loan: { cap_discount: cap_discto_available }});
    
    // setCuotaSol(cap_discto_available)
    setCuotaMax(cap_discto_available)
    setTasaSol(tasa)
    setTasaSol2(tasa2)
    setQuotation({ loan: { amount: undefined, debt_capacity, discount_capacity,
                            debt_capacity_mortgage, discount_capacity_mortgage }});

    // Edad máxima ghasta la que se puede prestar
    const yyyy = birth.getFullYear() + (gender==='male' ? 62 : 57)
    const f1 = moment((today.getFullYear()).toString()+m1.toString()+d1.toString())
    const f2 = moment(yyyy.toString()+m1.toString()+d1.toString())

    const dd = f2.diff(f1,'days')
    let plazo_age = Math.floor(dd/30.46153846)

    if(plazo_max > plazo_age) {
      plazo_max = plazo_age
    }
    setPlazoSol(plazo_max)
    setPlazoMax(plazo_max)

  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const detallePagos = (detPagos: IDetPagos[]) => {

    return (
      <Grid container justify='center'>
        <h1>Plan de Pagos</h1>
        <TableContainer component={Paper}>
          <Table size="small" aria-label="a dense table">
            <TableHead>
              <TableRow>
                <TableCell align="center">Cuota</TableCell>
                <TableCell align="center">Desde</TableCell>
                <TableCell align="center">Hasta</TableCell>
                <TableCell align="right">Dias</TableCell>
                <TableCell align="right">Letra</TableCell>
                <TableCell align="right">Tasa</TableCell>
                <TableCell align="right">Saldo Ant</TableCell>
                <TableCell align="right">Abono Cap</TableCell>
                <TableCell align="right">Interes</TableCell>
                <TableCell align="right">Abono Int</TableCell>
                <TableCell align="right">Interes Pendt</TableCell>
                <TableCell align="right">Saldo Actual</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {detPagos.map(cuota => 
                {
                  return (
                    <>
                      {cuota.dias > 0 ? (
                        <TableRow>
                        <TableCell align="center">{cuota.cuota}</TableCell>
                        <TableCell align="center">{cuota.desde}</TableCell>
                        <TableCell align="center">{cuota.hasta}</TableCell>
                        <TableCell align="right">{cuota.dias}</TableCell>
                        <TableCell align="right">{cuota.letra.toFixed(2)}</TableCell>
                        <TableCell align="right">{cuota.tasa.toFixed(2)}</TableCell>
                        <TableCell align="right">{cuota.saldoAnt.toFixed(2)}</TableCell>
                        <TableCell align="right">{cuota.pagoCap.toFixed(2)}</TableCell>
                        <TableCell align="right">{cuota.intCal.toFixed(2)}</TableCell>
                        <TableCell align="right">{cuota.pagoInt.toFixed(2)}</TableCell>
                        <TableCell align="right">{cuota.intPend.toFixed(2)}</TableCell>
                        <TableCell align="right">{cuota.saldoAct.toFixed(2)}</TableCell>
                      </TableRow>
                      ) 
                      : null}
                    </>
                  )
                }
              )}
            </TableBody>
          </Table>
        </TableContainer>
      </Grid>      
    )
  }

  
  return (
    <>
      {montoFinal > 0 ? (
        <Grid container justify='center'>
          <Grid item xs={12} sm={5} classes={{item: classes.content}}>
            <div className={classes.starsBackground}>
              <CheckCircleOutlineOutlinedIcon classes={{root: classes.icon}} />
            </div>
            <Typography 
              align='center' 
              variant='h5' 
              paragraph
            >
              {STRINGS.QUOTATION.PERSONAL_LOAN.LOAN.TITLE}
            </Typography>
            <Typography
              align='center' 
              color='textSecondary'
              classes={{root: classes.subtitle}}
            >
              {STRINGS.QUOTATION.PERSONAL_LOAN.LOAN.CONTENT(montoFinal || 0, plazoFinal || 0, cuotaFinal || 0, "personal")}
            </Typography>
          </Grid>
        </Grid>
      ) 
      : null
      }
      <Grid container spacing={2} justify='center'>
        <Grid item xs={12}> 
          <Grid item xs={6} classes={{item: classes.label}}>
            <FormLabel>{STRINGS.QUOTATION.PERSONAL_LOAN.LOAN.CHOOSE_AMOUNT}</FormLabel>
          </Grid>
          <Grid item xs={6}>
            <NumberField
              onChange={handleAmountChange}
              value={montoSol ? montoSol: ""}
            />
          </Grid>
        </Grid>
        <Grid item xs={12}>
          <Grid item xs={6} classes={{item: classes.label}}>
            <FormLabel>{"Plazo máximo en meses"}</FormLabel>
          </Grid>
          <Grid item xs={6}>
            <NumberField
              onChange={handleTermChange}
              onBlur={handleTermBlur}
              value={plazoSol ? plazoSol: ""}
            />
          </Grid>
        </Grid>
        <Grid container item xs={12}>
          <Grid container item xs={6}>
            <Grid item xs={12} classes={{item: classes.label}}>
              <FormLabel>Capacidad ({cuotaMax})</FormLabel>
            </Grid>
          </Grid>
        </Grid>
        <Grid container item xs={12}>
          <Grid container item xs={6}>            
            <Grid item xs={12} classes={{item: classes.label}}>
              <FormLabel>Letra Aproximada</FormLabel>
            </Grid>
            <Grid item xs={12}>
              <NumberField
                onChange={handleCuotaChange}
                // onBlur={handleCuotaBlur}
                disabled={true}
                value={cuotaFinal ? cuotaFinal: ""}
              />
            </Grid>
          </Grid>

          <Grid item xs={6}>
            <Grid item xs={12} classes={{item: classes.label}}>
              <FormLabel>Tasa</FormLabel>
            </Grid>
            <Grid item xs={12}>
              <NumberField
                onChange={handleTasaChange}
                value={tasaSol ? tasaSol: ""}
                disabled={true}
              />
            </Grid>
          </Grid>
        </Grid>

        <Grid container xs={12} md={8}>
          <Grid item xs={4} style={{marginTop: '25px'}}>
            <Button 
              color="primary" 
              variant="contained" 
              onClick={calculosPlazo}
              disabled={plazoSol < 11}
            >Calcular</Button>
          </Grid>
          <Grid item xs={8} style={{marginTop: '25px'}}>
            {
              detPagos.length > 0 &&
              <FormControlLabel
              control={<Checkbox checked={checked} color="primary" onChange={()=>setChecked(!checked)} />}
              label="Ver Plan de Pagos"
              />
            }
          </Grid>
        </Grid>
      </Grid>

      {/* MOSTRAR EL DETALLE DEL PLAN DE PAGOS */}
      { checked && detallePagos(detPagos) }
    </>
  );
};

export default Form